import logo from './logo.svg';
import './App.css';
import {useEffect,  useReducer, useState} from 'react';
import Card from "./Card";
import { reducer } from "./reducer";
import React from 'react';

export const CardContext = React.createContext();

const defaultState = [];


function App() {
  
  const [cards, dispatch] = useReducer(reducer, defaultState);

  const [activeFilter, setActiveFilter] = useState(false);

  useEffect(()=>{
    (async () => {
            let response =  await fetch('https://api.thecatapi.com/v1/images/search?limit=5&page=10&order=Descapi_key=live_q2l82GatdOfVpdqz2LzxnCGhjULcsEDxEUXS7sle5SdlfoMvhBKh0ATtgZkiyu1o')
            response = await response.json();
            dispatch({value: response, type: "SET"});
        })();
  }, [])

  return (
    <>
    <div id="filter" className={activeFilter ? "active" : ""}>
      <div onClick={()=>{
        setActiveFilter(!activeFilter)
      }}></div>
    </div>
    <div className="card-container">
    {cards ? (
      <CardContext.Provider value={{dispatch, cards}}>
      {cards.map((card, n)=>(
        <Card card={card} key={card.id} activeFilter={activeFilter}></Card>
      ))}
      </CardContext.Provider>
      ) :
      <></>}
    </div>
    </>
  );
}

export default App;
